.home{background-color:#becb05;width:100%;height:100%}.panel{border-radius:5px;background-color:rgba(0,0,0,.2);text-align:center}.panelTasks{position:absolute;top:220px;width:51.2%;height:180px;overflow:hidden;cursor:pointer;background-color:initial}.home__panel_loc-left{margin:20px 0 0 20px}.home__panel_loc-right{position:absolute;top:20px;right:20px}.home__panel_width{width:46.2%}.panel__time{font-size:90px;line-height:8rem}.panel__condition{font-size:22px;line-height:1.5rem;border-radius:5px 0 0 0;margin-top:5px}.panel__img{width:94px;margin-right:50px}.panel__temp{display:inline-block;vertical-align:top;font-size:5rem;line-height:6rem;font-weight:400}.panel__degree{font-size:2rem;line-height:4rem;vertical-align:top}.text{background-color:rgba(0,233,94,.6);font-size:1.5rem;line-height:2.5rem;text-align:center;border-radius:0 0 5px 5px}a{text-decoration:none;color:#fff}.eventSwitcher{padding:0 20px}.eventSwitcher__eventTitle{display:inline-block;font-size:1rem;line-height:1.2rem;padding:2px 6px}.eventSwitcher__eventTime{width:120px;text-align:center;font-weight:300;background-color:rgba(0,233,94,.6);border-radius:4px 0 0 4px;display:inline-block;font-size:1rem;line-height:1.2rem;padding:2px 6px}.eventSwitcher__event{padding:3px;margin:8px 0;background-color:rgba(0,0,0,.5);border-radius:10px;display:flex}.eventSwitcher__other{color:#ccc;font-size:.9rem;font-style:italic;padding:2px 6px;margin-left:20px}.eventSwitcher__checked .eventSwitcher__check:after{content:"✔";position:absolute;font-size:.9rem;top:-3px;left:2px}.eventSwitcher__checked .eventSwitcher__eventTitle{font-style:italic;text-decoration:line-through;color:#b3b3b3}.eventSwitcher__check{position:relative;margin:auto 2px;width:20px;height:20px;border:2px solid rgba(0,233,94,.6);border-radius:20px}.panelTasks_loc_right{right:0}