.tasks{background-color:#002e0f;width:100%;height:100%}.lists{position:absolute;top:10px;width:100%;height:76.8%;display:flex;justify-content:center}.formTasks__input{margin-top:15px;margin-left:10px;background-color:#60fca9}.formTasks__img{width:20px;height:20px;color:#ccc;vertical-align:middle}.eventSwitcher__removeImg{width:25px;height:25px;margin-right:5px;text-align:center;line-height:20px;vertical-align:middle}.formTasks__button{margin-left:10px}.list{display:inline-block;vertical-align:top;width:30%;margin:10px;background-color:rgba(0,0,0,.4);border-radius:5px}.list__header{text-align:center}.list__title{font-size:18px;line-height:32px;background-color:rgba(0,255,84,.7);border-radius:5px 5px 0 0}.list__newItem{padding:15px 15px 0 15px;height:35px}.list__text{cursor:pointer}.list__text_title{display:inline-block;color:#ccc;font-size:15px}.eventSwitcher{padding:10px 10px;overflow:auto;height:70%}.eventSwitcher__checked .eventSwitcher__check::after{content:"✔";position:absolute;font-size:17px;top:-2px;left:1px}.eventSwitcher__checked .eventSwitcher__title{font-style:italic;text-decoration:line-through;color:#b3b3b3}.eventSwitcher__check{position:relative;margin:auto;width:20px;height:20px;border:2px solid rgba(0,255,84,.7);border-radius:20px}.eventSwitcher__check,.eventSwitcher__title{display:inline-block;font-size:1rem;line-height:20px;vertical-align:top}.eventSwitcher__title{width:170px;padding:2px 6px}.eventSwitcher__remove{padding:0}.eventSwitcher__img{width:20px;height:20px;margin-right:5px;text-align:center;color:#ccc;line-height:20px;vertical-align:middle}.eventSwitcher_event{display:flex;padding:0;margin:8px 0;border-radius:10px;cursor:pointer}.eventSwitcher::-webkit-scrollbar{height:10px;width:10px}.eventSwitcher::-webkit-scrollbar-thumb{border-radius:10px;background-color:#002e0f;border:1px solid #062}.eventSwitcher::-webkit-scrollbar-corner{background:#002e0f;display:none;opacity:0}